.blinking {
  -webkit-animation: blinkingText 1s infinite;
          animation: blinkingText 1s infinite; }

@-webkit-keyframes blinkingText {
  0% {
    color: orange; }
  49% {
    color: transparent; }
  50% {
    color: transparent; }
  80% {
    color: transparent; }
  100% {
    color: orange; } }

@keyframes blinkingText {
  0% {
    color: orange; }
  49% {
    color: transparent; }
  50% {
    color: transparent; }
  80% {
    color: transparent; }
  100% {
    color: orange; } }

.active {
